@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap);
.button{color:#b1b4ba;font-family:"Montserrat",sans-serif;cursor:pointer;outline:none;transition:color 250ms;background-color:#141414;padding:8px 15px;margin:5px;border:1px solid #b1b4ba;border-radius:10px}.button:hover,.button:focus{color:#eedb00;border-color:#eedb00}.input-num-field,.dropdown{color:#141414;background:#b1b4ba;font-family:"Montserrat",sans-serif;margin:10px;letter-spacing:2px;border:1px solid #b1b4ba;border-radius:10px;outline:none}.input-num-field{font-size:1rem;text-align:center;padding:6px 3px;border:1px solid #b1b4ba}.dropdown{padding:5px}.amount-selector-container,.spaceships-selector-container,.starship-element{display:flex;flex-direction:column;justify-content:center;align-items:center;padding:2rem;margin:2rem;border:1px solid #eedb00;border-radius:25px;background-color:#414042}.starship-element-text{text-align:center;margin:5px}.starship-element-title{color:#eedb00;font-size:0.8rem}.starship-element-characteric{margin-top:5px}*{padding:0;margin:0;box-sizing:border-box}html,.main-container{color:#b1b4ba;background-color:#414042;font-family:"Montserrat",sans-serif;width:100%;height:100vh;background-image:url(/sw-spaceships/static/media/space.91681c57.jpg);background-size:100%}.amount-selector-text,.spaceships-selector-text{text-align:center;margin:0.5rem;font-size:1.2rem}.input-num-form{margin:0.5rem}.dropdown-form{display:flex;flex-direction:column;justify-content:center}.dropdowns-container{text-align:center}.output-container{width:100%;display:grid;grid-template-columns:repeat(auto-fill, minmax(400px, 1fr));justify-content:center}

